@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lora:ital@1&family=PT+Sans:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body
{
  overflow: hidden;
}

.App-header {
  background-color: #222222;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}


.particule {
  overflow: hidden;
  position: absolute;
  top: 0px;
  left: 0px;
  bottom: 0px;
  right: 0px;
  z-index: 10;
}


#welcome
{
  z-index: 99;
}

.ml1 {
    font-weight: 900;
    font-size: 3.5em;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    height: 100vh;

    position: absolute;
    top: 0px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    margin: 0px;
  
    background-color: #222222;
  }
  
  .ml1 .letter {
    display: inline-block;
    line-height: 1em;
  }
  
  .ml1 .text-wrapper {
    position: relative;
    display: inline-block;
    padding-top: 0.1em;
    padding-right: 0.05em;
    padding-bottom: 0.15em;
  }
  
  .ml1 .line {
    opacity: 0;
    position: absolute;
    left: 0;
    height: 3px;
    width: 100%;
    background-color: #fff;
    transform-origin: 0 0;
  }
  
  .ml1 .line1 { top: 0; }
  .ml1 .line2 { bottom: 0; }


  

#home
{
    background-color:rgba(255, 0, 0, 0.1);
    
    z-index: 40;

    height: 550px;
    width: 375px;
 
    border-radius: 10px;
}


#postionHome
{
    position: relative;
    top: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: flex-start;
    align-items: center;

}


@media all and (min-device-width: 500px)
{
    #home
    {
        width: 700px;
    }

}


#image
{
    background-image: url(/static/media/Avatar.0b1a0a76.jpg);
    width: 225px;
    height: 225px;
    border-radius: 50%;
    background-size: 120%;
    background-position: center;
    background-position-y: -60px;
    z-index: 55;
}



.text
{
    display: block;
    text-align: center;

}


.titre
{
    font-size: 4em;
    font-weight: 600;
    margin: 10px;
    font-family: 'PT Sans', sans-serif;
}

.SouTitre
{
    font-size: 1em;
    opacity: 0.7;
    margin: 0px;
    position: relative;
    top: -10px;
    font-family: 'Lora', serif;
}

.logo
{
    margin-top: 10px;

    display: flex;
    height: 100%;
    width:  100%;
    flex-direction: row;
    align-content: center;
    justify-content: center;
    align-items: center;
}


.logo a svg
{
    padding: 25px;
}

